module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-back-top-custom',\r\n  template: `\r\n    <nz-back-top [nzTemplate]=\"tpl\" [nzVisibilityHeight]=\"100\" (nzOnClick)=\"notify()\">\r\n      <ng-template #tpl>\r\n        <div class=\"ant-back-top-inner\">UP</div>\r\n      </ng-template>\r\n    </nz-back-top>\r\n    Scroll down to see the bottom-right\r\n    <strong> blue </strong>\r\n    button.\r\n  `,\r\n  styles  : [ `\r\n    :host ::ng-deep .ant-back-top {\r\n      bottom: 100px;\r\n    }\r\n\r\n    :host ::ng-deep .ant-back-top-inner {\r\n      height: 40px;\r\n      width: 40px;\r\n      line-height: 40px;\r\n      border-radius: 4px;\r\n      background-color: #1088e9;\r\n      color: #fff;\r\n      text-align: center;\r\n      font-size: 20px;\r\n    }\r\n\r\n    :host ::ng-deep strong {\r\n      color: #1088e9;\r\n    }\r\n  ` ]\r\n})\r\nexport class NzDemoBackTopCustomComponent {\r\n  notify(): void {\r\n    console.log('notify');\r\n  }\r\n}\r\n"